import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import {
  IAddEditBranchModel,
  IBranchesViewModel,
  IBranchModel,
  IDistrictDropModel,
} from "app/admin/models/roles/function";
import { BranchesService } from "app/admin/services/roles/branches.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { MessagesService } from "app/shared/services/messages.service";
import { LanguageService } from "app/shared/services/language.service";

@Component({
  selector: "app-center-branches",
  templateUrl: "./center-branches.component.html",
  styleUrls: ["./center-branches.component.scss"],
})
export class CenterBranchesComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];
  itemsCount: number = 0;
  curPage: number = 1;

  branches: IBranchModel[] = [];
  districts: IDistrictDropModel[] = [];

  displayedColumns: string[] = ["#", "Name", "districtName", "actions"];
  dataSource: MatTableDataSource<IBranchModel>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private route: ActivatedRoute,
    private fb: FormBuilder,
    public authserv: AuthService,
    private msg: MessagesService,
    private branchService: BranchesService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    public languageService: LanguageService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //console.log(`row Functions for"${this.functionId}": `, res);
            }
          });
      }
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      branchId: [0],
      branchName: ["", [Validators.required, CustomeValidator.whiteSpace]],
      //address: [null],
      districtId: ["", Validators.required],
    });

    this.getAllBranches(this.curPage);
    this.getAllRegions();
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllBranches(1);
      this.dataSource.paginator = this.paginator;
    });

    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  getAllRegions() {
    this.branchService
      .getAllDistricts<IDistrictDropModel[]>()
      .subscribe((response) => {
        if (response) {
          // console.log("districts: ", response);
          this.districts = response;
        }
      });
  }

  getAllBranches(pageNumber: number) {
    this.spinner.show();
    this.curPage = pageNumber;
    // console.log("Educom", this.EduCompId);
    this.branchService
      .getAllAllCenterBranches<IBranchesViewModel>(this.EduCompId, pageNumber)
      .subscribe((response) => {
        if (response) {
          // console.log("branches: ", response);
          this.branches = response.centerBrnachesList;
          this.dataSource = new MatTableDataSource(response.centerBrnachesList);
          this.itemsCount = response.itemsCount;
          this.dataSource.sort = this.sort;
        }
        this.spinner.hide();
      });
  }

  editBranch(branch: IBranchModel) {
    this.FormCtrls.branchId?.setValue(branch.Id);
    this.FormCtrls.branchName?.setValue(branch.Name);
    //this.FormCtrls.address?.setValue(branch.address);
    this.FormCtrls.districtId?.setValue(branch.districtId);
  }

  removeBranch(branch: IBranchModel) {
    this.spinner.show();
    this.branchService.deleteBranch(branch.Id).subscribe((response: any) => {
      if (response) {
        this.msg.success("General.DeleteSuccessfully");
        if (this.dataSource.data.length == 1 && this.curPage > 1) {
          // if last item in list: get previous page
          this.getAllBranches(this.curPage - 1);
          this.paginator.pageIndex = this.curPage - 1;
        } else {
          // if not last item in list: get current page
          this.getAllBranches(this.curPage);
        }
        if (branch.Id == this.FormCtrls.branchId.value) this.clearForm();
        // console.log("user: ", response);
      } else this.msg.error(response.returnString);
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;

    if (this.myForm.valid) {
      this.spinner.show();
      let obj = {} as IAddEditBranchModel;
      obj.Id = this.FormCtrls.branchId?.value;
      obj.Name = this.FormCtrls.branchName?.value;
      obj.districtId = this.FormCtrls.districtId?.value;
      obj.EduCompId = this.EduCompId;
      //  console.log("NOW CALL SERVER: ", obj);

      this.branchService
        .addEditBranches<IAddEditBranchModel, any>(obj)
        .subscribe((response) => {
          if (response && response.returnValue === 200) {
            // console.log("created: ", response);
            if (obj.Id == 0) {
              this.getAllBranches(1); // first page after add
              this.dataSource.paginator = this.paginator;
            } else {
              this.getAllBranches(this.curPage); // current page after update
            }

            this.msg.success(
              obj.Id == 0
                ? "General.AddSuccessfully"
                : "General.UpdateSuccessfully"
            );

            this.clearForm();
          } else this.msg.error(response?.returnString);
          this.spinner.hide();
        });
    } else this.msg.error("General.FormNotValid");
  }

  openDeleateDialog(branch: IBranchModel): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: branch.Name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeBranch(branch);
      });
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.branchId.setValue(0);
    this.submitted = false;
  }
}
