import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { ICategories } from "app/admin/models/admin/course-request";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class CategoriesService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getCategories<T>(page: number) {
    return this.get<T>(`courseCategory/getAllCategoriesByPage/${page}`);
  }

  getCategoriesDropdown() {
    return this.get<ICategories>(`courseCategory/getAllCategories`);
  }
  getCategoriesForEmployee() {
    return this.get<ICategories>(`courseCategory/getAllCategories`);
  }

  deleteCategories(id: number) {
    return this.delete(`courseCategory/DeleteCategories/${id}`);
  }

  editCategories<T>(model: T, id: number) {
    return this.put<T>(`courseCategory/PutCategories/${id}`, model);
  }

  addCategories<T>(model: T) {
    return this.post<T>(`courseCategory/PostCategories/`, model);
  }
}
