import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { ISearchMaterialVM } from "app/admin/models/admin/educations";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class SubjectMaterialsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getParts2URL = "Admin/returnMaterialPartsByMaterialId/";
  AttendanceURL = "Admin/postOfflineLectureView/";
  materialsattacAttendeesURL = "MaterialsAttach/offline-lecture-attendees/";

  addEditMaterial<T>(data: T) {
    return this.post(`Admin/AddSubjectMaterialForm`, data);
  }

  getMaterialById<T>(subjectId: any, materialId: any) {
    return this.get<T>(
      `Admin/showSubjectMaterialView/${subjectId}/${materialId}`
    );
  }

  updateMaterialForm(EduCompId: any, data: any) {
    return this.post(`Admin/AddSubjectMaterialForm/${EduCompId}`, data);
  }

  deleteMaterial(materialId: any) {
    return this.delete(`Admin/RemoveMaterial/${materialId}`);
  }

  publishMaterial(materialId: any) {
    return this.get(`Admin/PublishDepublishhMaterial/${materialId}`);
  }

  getGroupsByEduCompId<T>(EduCompId: any) {
    return this.get<T>(
      `Admin/returnListEduCompIdGroupByEduCompID/${EduCompId}`
    );
  }

  returnSearchedUnitsBySubjectId<T>(subjectId: any) {
    return this.get<T>(`Admin/returnSearchedUnitsBySubjectId/${subjectId}`);
  }

  returnSearchedLessonsByUnitId<T>(UnitId: any) {
    return this.get<T>(`Admin/returnSearchedLessonsByUnitId/${UnitId}`);
  }

  searcheMaterial<T>(data: ISearchMaterialVM) {
    return this.post<T>(`Admin/returnSearchedMaterialViewResult`, data);
  }

  showVideoByMaterialId(materialId: any) {
    return this.get(`Admin/showVideoByMaterialId/${materialId}`);
  }

  dawnloadLectureAttendees(materialId: any) {
    return this.get(`Admin/offlineLectureAttendees/${materialId}`);
  }

  getMaterialParts(materialId: any) {
    return this.get(`Admin/materialPartsTable/${materialId}`);
  }

  getPartById<T>(partId: any) {
    return this.get<T>(`Admin/getMaterialPartByID/${partId}`);
  }

  addPart(data: any) {
    return this.post("Admin/AddMaterialPart", data);
  }

  // updatePart(data: any) {
  //   return this.post(this.updatePartURL, data);
  // }

  deletePart(partId: any) {
    return this.post(`Admin/RemoveMaterialPart/${partId}`, null);
  }

  getMaterialAttaches(materialId: any) {
    return this.get(`Admin/materialAttachsTable/${materialId}`);
  }

  addAttach(data: any) {
    return this.post("Admin/AddMaterialAttach", data);
  }

  deleteAttach(AttachId: any) {
    return this.post(`Admin/RemoveMaterialAttach/${AttachId}`, null);
  }

  getExamsAndTemplates(subjectId: number) {
    return this.get(`Admin/showMaterialQuiz/${subjectId}`);
  }

  getMaterialQuizes<T>(materialId: any) {
    return this.get<T>(`Admin/returnExamsByMaterialId/${materialId}`);
  }

  addMaterialQuiz(materialIdHiddenQuiz: any, data: any) {
    return this.post(`Admin/AddQuizForm/${materialIdHiddenQuiz}`, data);
  }

  deleteMaterialQuiz(Id: any) {
    return this.delete(`Admin/RemoveMaterialQuiz/${Id}`);
  }

  AddPopQuestion(materialId: any, body: any) {
    return this.post(`Admin/AddPopQuestion/${materialId}`, body);
  }

  getPopQuestions(materialId: any) {
    return this.get(
      `Admin/returnMaterialPopQuestionsByMaterialId/${materialId}`
    );
  }

  deletePopQuestion(id: any) {
    return this.post(`Admin/DeleteMaterialPopQuestion/${id}`, null);
  }

  UploadStudentAttendance(LectureID: any, attendance: any, data: any) {
    return this.post(
      `Admin/postOfflineLectureAttendees/${attendance}/${LectureID}`,
      data
    );
  }

  offlineLectureAttendeesTable<T>(LectureID: any, page: any) {
    return this.get<T>(
      `Admin/offlineLectureAttendeesTable/${LectureID}/${page}`
    );
  }

  searchAttendanceManual(data: any) {
    return this.post("Admin/offlineLectureAttendees", data);
  }

  updateAttendanceManual(data: any) {
    return this.post("Admin/editOfflineLectureAttendeeState", data);
  }
}
