import { DatePipe } from "@angular/common";
import { Component, OnInit, ViewChild } from "@angular/core";
import {
  AbstractControlOptions,
  FormBuilder,
  FormGroup,
  Validators,
} from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { ArticlesService } from "app/admin/services/Admin/setup/articles.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";
import { EditJobTitleDatesComponent } from "app/shared/components/dialogs/edit-job-title-dates/edit-job-title-dates.component";
import {
  IAddEditArticleModel,
  IArticleJobTitleGetVM,
  IArticleModel,
  IArticleViewModel,
} from "app/admin/models/admin/setup/articles";
import { IJobTitleDatesEditVM } from "app/admin/models/setup/job-title";
import { MessagesService } from "app/shared/services/messages.service";
import { LanguageService } from "app/shared/services/language.service";

@Component({
  selector: "app-articles",
  templateUrl: "./articles.component.html",
  styleUrls: ["./articles.component.scss"],
})
export class ArticlesSetupComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  curPage: number = 1;
  functionId: number;
  EduCompId: number;
  rowFunctions: IRowFunctionVM[];
  currentDate = new Date();
  displayedColumns: string[] = [
    "#",
    "title",
    "department",
    "jobTitle",
    "option",
  ];
  dataSource: MatTableDataSource<IArticleModel>;
  initTable: boolean = true;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private toastr: MessagesService,
    private articleService: ArticlesService,
    public authserv: AuthService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public languageService: LanguageService,
    private datePipe: DatePipe
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group(
      {
        id: [0],
        title: ["", [Validators.required, CustomeValidator.whiteSpace]],
        ArticleContent: ["", [Validators.required]],
        depJobIds: [{ value: "", disabled: true }, [Validators.required]],
        departmentId: ["", [Validators.required]],
        from_date: [null],
        to_date: [null],
      },
      {
        validator: CustomeValidator.dateBefore("from_date", "to_date"),
      } as AbstractControlOptions
    );

    this.getAllArticles(this.curPage);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllArticles(1);
      this.dataSource.paginator = this.paginator;
    });
  }

  startSortingTable() {
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "department":
          return this.languageService.isArabic
            ? item.department.ar_name ?? item.department?.en_name
            : item.department?.en_name ?? item.department?.ar_name;
        case "jobTitle":
          return this.languageService.isArabic
            ? item.jobTitle[0]?.ar_name ?? item.jobTitle[0]?.en_name
            : item.jobTitle[0]?.en_name ?? item.jobTitle[0]?.ar_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  getAllArticles(curPageber: number) {
    this.curPage = curPageber;
    this.spinner.show();
    this.articleService
      .getAllArticles<IArticleViewModel>(curPageber, this.EduCompId)
      // .getAllArticles<IArticleViewModel>(this.EduCompId, curPageber)
      .subscribe((response) => {
        if (response) {
          this.dataSource = new MatTableDataSource(response.ArticleList);
          this.itemsCounts = response.countItems;
          // console.log("articles: >> ", response);
          this.startSortingTable();
        }
        this.spinner.hide();
      });
  }

  onSubmit() {
    this.submitted = true;

    if (this.myForm.valid) {
      let article = {} as IAddEditArticleModel;
      article.EduCompId = this.EduCompId;
      article.title = this.FormCtrls.title?.value;
      article.ArticleContent = this.FormCtrls.ArticleContent?.value;
      article.depJobIds = this.FormCtrls.depJobIds?.value;
      article.from_date = this.datePipe.transform(
        this.FormCtrls.from_date?.value ?? this.currentDate
      );
      article.to_date = this.datePipe.transform(this.FormCtrls.to_date?.value);
      // console.log("edit or add => ", article);

      this.spinner.show();
      ////////////////// add & edit article
      this.FormCtrls.id?.value == 0
        ? this.addArticle(article)
        : this.updateArticle(article);
    } else this.toastr.error("General.FormNotValid");
  }

  addArticle(article: IAddEditArticleModel) {
    this.articleService
      .addArticle<IAddEditArticleModel, any>(article)
      .subscribe((response) => {
        if (response && response !== null) {
          if (response.returnValue == 0) {
            this.toastr.error(response.returnString);
          } else {
            this.getAllArticles(1);
            this.dataSource.paginator = this.paginator;
            this.toastr.success("General.AddSuccessfully");
            this.clearForm();
          }
        }
        this.spinner.hide();
      });
  }

  updateArticle(article: IAddEditArticleModel) {
    this.articleService
      .editArticle<IAddEditArticleModel, any>(article, this.FormCtrls.id?.value)
      .subscribe((response) => {
        if (response && response !== null) {
          if (response.returnValue == 0) {
            this.toastr.error(response.returnString);
          } else {
            this.getAllArticles(this.curPage);
            this.toastr.success("General.UpdateSuccessfully");
            this.clearForm();
          }
        }
        this.spinner.hide();
      });
  }

  editArticle(article: IArticleModel) {
    // console.log(article);
    this.myForm.patchValue(article);
    this.FormCtrls.departmentId?.setValue(article.department.id);
    this.FormCtrls.depJobIds?.setValue(article.jobTitle.map((j) => j.depJobId));
  }

  openDeleateDialog(article: IArticleModel): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: article.title,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeArticle(article);
      });
  }

  removeArticle(article: IArticleModel) {
    this.articleService.deleteArticle(article.id).subscribe((response: any) => {
      if (response.returnValue == 1) {
        // console.log("articlr to delete", response);
        this.toastr.success("General.DeleteSuccessfully");
        if (this.dataSource.data.length == 1 && this.curPage > 1) {
          // if last item in list: get previous page
          this.getAllArticles(this.curPage - 1);
          this.paginator.pageIndex = this.curPage - 1;
        } else {
          // if not last item in list: get current page
          this.getAllArticles(this.curPage);
        }
        if (article.id == this.FormCtrls.id.value) this.clearForm();
      } else this.toastr.error(response.returnString);
      this.spinner.hide();
    });
  }

  editJobTitleDates(
    jobTitle_articleId: number,
    jobtitle: IJobTitleDatesEditVM
  ) {
    this.articleService
      .editJobTitleArticle<IJobTitleDatesEditVM, any>(
        jobTitle_articleId,
        jobtitle
      )
      .subscribe((response) => {
        if (response.returnValue == 1) {
          this.getAllArticles(this.curPage);
          this.toastr.success("General.UpdateSuccessfully");
        } else this.toastr.error(response.returnString);
      });
  }

  openUpdateJobTitleDialog(jobTitle: IArticleJobTitleGetVM) {
    // console.log(jobTitle);
    this.dialog
      .open(EditJobTitleDatesComponent, {
        data: {
          jobTitle: jobTitle,
        },
      })
      .afterClosed()
      .subscribe((response) => {
        if (response) {
          // console.log(response.jobTitle);
          this.editJobTitleDates(jobTitle.jobTitle_article, response.jobTitle);
        }
      });
  }

  openDeleteJobtitleDialog(jobTitle: IArticleJobTitleGetVM) {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.languageService.isArabic
            ? jobTitle.ar_name ?? jobTitle.en_name
            : jobTitle.en_name ?? jobTitle.ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeJobtitleArticle(jobTitle.jobTitle_article);
      });
  }

  removeJobtitleArticle(jobTitle_article: number) {
    this.articleService
      .deleteJobTitleArticle(jobTitle_article)
      .subscribe((response: any) => {
        if (response.returnValue == 1) {
          this.getAllArticles(this.curPage);
          this.toastr.success("General.DeleteSuccessfully");
        } else this.toastr.error(response.returnString);
      });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
  }
}
