  // onDeselectDepartment(index: number, id: number) {
  //   this.departmentDatesCtrl.removeAt(index);
  //   let indexOfId = this.formCtrl.departmentsIds.value.findIndex(
  //     (e) => e == id
  //   );
  //   this.formCtrl.departmentsIds.value.splice(indexOfId, 1);
