import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class RolesService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getAllRoles<T>(EduCompId: number) {
    return this.get<T>("usermanagement/returnRoles" + "/" + EduCompId);
  }

  addEditRole<T, TV>(obj: T) {
    return this.post<TV>(`usermanagement/create-role`, obj);
  }

  deleteRole(roleId: number) {
    return this.delete("usermanagement/deleteRole" + "/" + roleId);
  }

  getAllFunctions<T>(page: number, getAll: boolean) {
    return this.get<T>(`usermanagement/getAllFunctions/${page}/${getAll}`);
  }

  getAllRoleFunctions<T>(roleId: number) {
    return this.get<T>(`usermanagement/returnRoleFunctionsByRoleID/${roleId}`);
  }

  addEditRoleFunctions<T, TV>(obj: T) {
    return this.post<TV>("usermanagement/add-function-to-role", obj);
  }

  getAllFunctionsByRoleId<T>(roleId: number) {
    return this.get<T>(`usermanagement/returnRoleFunctionsByRoleID/${roleId}`);
  }

  getAllUserBranchesWithFunctions<T>(userId: number) {
    return this.get<T>(
      `usermanagement/returnAllBranchUserAllRoleFunction/${userId}`
    );
  }

  addUserBranchesAndRoleFunctions<T, TV>(obj: T) {
    return this.post<TV>("usermanagement/addUserBranchesAndRoleFunctions", obj);
  }

  deleteUserRoleFunctions(functionId: number) {
    return this.delete(
      `usermanagement/deleteBranchUserRoleFunction/${functionId}`
    );
  }

  deleteUserBranchRoleFunction(userId: number, branchId: number) {
    return this.delete(
      `usermanagement/deleteBranchUserAllRoleFunction/${userId}/${branchId}`
    );
  }
}
