import { AppAssets } from "app/shared/data/app-assets";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { GiftsService } from "app/admin/services/Admin/gifts.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { MessagesService } from "app/shared/services/messages.service";
import { LanguageService } from "app/shared/services/language.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { addgiftViewModel } from "app/admin/models/admin/gifts";

@Component({
  selector: "app-gifts-add",
  templateUrl: "./gifts-add.component.html",
  styleUrls: ["./gifts-add.component.scss"],
})
export class GiftsAddComponent implements OnInit {
  functionId: number;
  submitted = false;
  giftId: number = 0;
  EduCompId: any;

  rowFunctions: IRowFunctionVM[];
  myForm!: FormGroup;
  currentLang: string;
  giftArName: string;
  giftEnName: string;
  giftCategory: any[];

  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private authserv: AuthService,
    private giftsService: GiftsService,
    private languageService: LanguageService,
    private spinner: NgxSpinnerService,
    private msg: MessagesService,
    public fileService: UploadFileService,
    private router: Router,
    public appAssets: AppAssets
  ) {
    this.route.queryParamMap.subscribe((params) => {
      this.giftId = Number(params.get("giftId"));
      if (this.giftId > 0) {
        this.getGiftById(this.giftId);
      }

      this.functionId = Number(params.get("functionId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //  console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });

    this.EduCompId = this.authserv.getEduCompId();
  }

  getAllGiftCategory() {
    this.giftsService
      .getGiftCategoryDropDown(this.EduCompId)
      .subscribe((response: any) => {
        if (response) {
          this.giftCategory = response;
          // console.log(this.giftCategory);
        }
      });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  get engNameCtrl() {
    return this.myForm.get("englishName");
  }
  get arbNameCtrl() {
    return this.myForm.get("arabicName");
  }
  get categoryCtrl() {
    return this.myForm.get("giftsCategoryId");
  }
  get pointsCtrl() {
    return this.myForm.get("points");
  }
  get qtyCtrl() {
    return this.myForm.get("qty");
  }
  get descriptionCtrl() {
    return this.myForm.get("description");
  }
  get IdCtrl() {
    return this.myForm.get("id");
  }

  ngOnInit() {
    this.myForm = this.fb.group({
      id: [""],
      giftPicture: [null],
      englishName: ["", [Validators.required, CustomeValidator.whiteSpace]],
      arabicName: ["", [Validators.required, CustomeValidator.whiteSpace]],
      giftsCategoryId: ["", Validators.required],
      points: ["", Validators.required],
      qty: ["", Validators.required],
      description: [""],
      // EduCompId: [this.EduCompId],
    });

    this.getAllGiftCategory();
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllGiftCategory();
    });
  }

  OnSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let gift: addgiftViewModel = {
        id: this.FormCtrls.id.value,
        arabicName: this.FormCtrls.arabicName.value,
        englishName: this.FormCtrls.englishName.value,
        giftsCategoryId: this.FormCtrls.giftsCategoryId.value,
        points: this.FormCtrls.points.value,
        qty: this.FormCtrls.qty.value,
        description: this.FormCtrls.description.value,
        giftPicture: this.FormCtrls.giftPicture.value,
        eduCompId: this.EduCompId,
      };
      // console.log(this.myForm.value);

      this.spinner.show();
      // console.log("form data", this.myForm.value);
      this.giftsService.addGift(gift).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.msg.success(
            this.IdCtrl.value == 0
              ? "General.AddSuccessfully"
              : "General.UpdateSuccessfully"
          );
          this.router.navigate(["dashboard/setup/gift-index/"], {
            queryParams: {
              functionId: this.functionId,
            },
          });
        } else this.msg.error(res?.returnString);
        this.spinner.hide();
      });
    } else this.msg.error("General.FormNotValid");
  }

  getGiftById(giftId: number) {
    this.giftsService.getGiftById(giftId).subscribe((res: any) => {
      this.giftArName = res.arabicName;
      this.giftEnName = res.englishName;

      // this.giftId = res.Id;
      this.IdCtrl?.setValue(this.giftId);
      // console.log("idc", this.IdCtrl.value);
      this.engNameCtrl.setValue(res?.englishName);
      this.arbNameCtrl.setValue(res?.arabicName);
      this.categoryCtrl.setValue(res?.GiftCategoryId);
      this.pointsCtrl.setValue(res?.points);
      this.qtyCtrl.setValue(res?.qty);
      this.descriptionCtrl.setValue(res?.description);
      this.FormCtrls.giftPicture.setValue(res.attach_path);
    });
  }

  DeletePic() {
    if (this.FormCtrls.giftPicture.value.FileAsBase64)
      return this.removeImage();

    this.spinner.show();
    this.giftsService.deleteAttach(this.IdCtrl.value).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.msg.success("General.DeleteSuccessfully");
        this.removeImage();
      } else this.msg.error(res);
      this.spinner.hide();
    });
  }

  removeImage() {
    this.FormCtrls.giftPicture.setValue(null);
    this.fileService.resetFileById("gift-photo");
  }
}
