import { Component, OnInit } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";
import {
  IFunctionsModel,
  IRoleFunctionsModel,
  IAddEditRoleFunctionsModel,
  IGroupFunctionsModel,
} from "app/admin/models/roles/roles";
import { RolesService } from "app/admin/services/roles/roles.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-role-functions",
  templateUrl: "./role-functions.component.html",
  styleUrls: ["./role-functions.component.scss"],
})
export class RoleFunctionsComponent implements OnInit {
  roleId: number;
  functionId: number;

  functions: IFunctionsModel[] = [];
  groupFunctions: IGroupFunctionsModel[] = [];
  roleFunctions: IRoleFunctionsModel[] = [];
  rolefunctionIds: number[] = [];
  roleName: string = "";
  allChecked: boolean = false;
  allFunctionsLength = 0;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private msg: MessagesService,
    private roleService: RolesService,
    private spinner: NgxSpinnerService
  ) {
    this.route.paramMap.subscribe((param) => {
      this.roleId =Number( param.get("roleId"));

    });
    this.route.queryParamMap.subscribe((param) => {
      this.functionId =Number( param.get("parentId"));

    });

  }

  ngOnInit(): void {
    this.getAllFunctions();
    this.getAllRoleFunctions();
  }

  getAllFunctions() {
    this.spinner.show();
    this.roleService
      .getAllFunctions<IGroupFunctionsModel[]>(0, true)
      .subscribe((response) => {
        if (response) {
          this.groupFunctions = response;
          this.groupFunctions.forEach((g) => {
            this.allFunctionsLength =
              this.allFunctionsLength + g.functions.length;
          });
        }
        this.spinner.hide();
      });
  }

  getAllRoleFunctions() {
    this.spinner.show();
    this.roleService
      .getAllRoleFunctions<IRoleFunctionsModel[]>(this.roleId)
      .subscribe((response) => {
        if (response) {
          this.roleFunctions = response;
          this.roleName = this.roleFunctions[0].roleName;

          for (let i = 0; i < this.roleFunctions.length; i++) {
            this.rolefunctionIds.push(this.roleFunctions[i].function.Id);
          }

          if (this.rolefunctionIds.length === this.allFunctionsLength)
            this.allChecked = true;
          else this.allChecked = false;
          //  console.log("edit functions IDs", this.rolefunctionIds);
        }
        this.spinner.hide();
      });
  }

  onFunctionToogle(functionId: number, $event: any) {
    if ($event.checked === true) {
      this.rolefunctionIds.push(functionId);
    } else if ($event.checked === false) {
      let index = this.rolefunctionIds.findIndex((id) => id === functionId);
      this.rolefunctionIds.splice(index, 1);
    }

    if (this.rolefunctionIds.length === this.allFunctionsLength)
      this.allChecked = true;
    else this.allChecked = false;
  }

  SelectAll() {
    this.allChecked = !this.allChecked;
    if (this.allChecked) {
      this.groupFunctions.forEach((g) => {
        g.functions.forEach((e) => {
          let found = this.rolefunctionIds.includes(e.Id);
          if (!found) this.rolefunctionIds.push(e.Id);
        });
      });
    } else {
      this.rolefunctionIds = [];
    }
  }

  onAddFunctions() {
    let obj = {} as IAddEditRoleFunctionsModel;
    obj.roleId = this.roleId;
    obj.functionsList = this.rolefunctionIds;
    // if (obj.functionsList.length == 0) {
    //   this.msg.warning("     ");
    //   return;
    //   } else {
    this.spinner.show();
    this.roleService
      .addEditRoleFunctions<IAddEditRoleFunctionsModel, any>(obj)
      .subscribe((response) => {
        if (response && response.returnValue === 200) {
          this.msg.success("General.AddSuccessfully");
          ///  console.log("add/edit functions: ", response);
        }
        this.spinner.hide();
      });
    //  }
  }
}
