import { ActivatedRoute, Router } from "@angular/router";
import { Component, OnInit, ViewChild } from "@angular/core";
import {
  AbstractControlOptions,
  FormBuilder,
  FormGroup,
  Validators,
} from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { IRolesViewModel } from "app/admin/models/roles/roles";
import {
  IAddEditUserModel,
  IUserModel,
  IUserSearchVM,
  IUserViewModel,
} from "app/admin/models/roles/user";
import { RolesService } from "app/admin/services/roles/roles.service";
import { UsersService } from "app/admin/services/roles/users.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AppConstants } from "app/shared/data/app_constants";
import { MessagesService } from "app/shared/services/messages.service";
import { LanguageService } from "app/shared/services/language.service";

@Component({
  selector: "app-users",
  templateUrl: "./users.component.html",
  styleUrls: ["./users.component.scss"],
})
export class UsersComponent implements OnInit {
  myForm: FormGroup;
  EduCompId: number;
  submitted: boolean = false;
  // // roles: IRolesViewModel[] = [];
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  itemsCounts: number = 0;
  items: number = 16184;
  curPage: number = 1;

  displayedColumns: string[] = [
    "#",
    "ar_name",
    "account_email",
    "mobile",
    // "role",
    "code",
    "option",
  ];
  dataSource: MatTableDataSource<IUserModel>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private toastr: MessagesService,
    private userService: UsersService,
    public authserv: AuthService,
    // // private roleService: RolesService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    // private router: Router,
    public appConstants: AppConstants,
    public languageService: LanguageService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //  console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });
    this.route.data.subscribe((val) => {
      console.log("view:-", val);
    });

    this.EduCompId = Number(this.authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group(
      {
        userId: [0, [Validators.required]],
        userName: [
          null,
          [
            Validators.required,
            Validators.pattern(
              /^[a-zA-Z \u0600-\u065F\u066A-\u06EF\u06FA-\u06FF]+$/
            ),
            CustomeValidator.whiteSpace,
          ],
        ],
        email: [null, [Validators.required, Validators.email]],
        phone: [
          null,
          [
            Validators.required,
            Validators.pattern(/^(\(?\+?[0-9]*\)?)?[0-9_ \-\(\)\S*$]*$/),
            Validators.maxLength(this.appConstants.MobileLength),
            Validators.minLength(this.appConstants.MobileLength),
            CustomeValidator.MobileStartsWith,
          ],
        ],
        // // roleId: [null, [Validators.required]],
        password: [
          null,
          [
            Validators.required,
            CustomeValidator.whiteSpace,
            Validators.minLength(3),
          ],
        ],
        confirmedPass: [null],
        code: [null, [Validators.required, CustomeValidator.whiteSpace]],
      },
      {
        validator: CustomeValidator.mustMatch("password", "confirmedPass"),
      } as AbstractControlOptions
    );

    // // this.roleService
    // //   .getAllRoles<IRolesViewModel[]>(this.EduCompId)
    // //   .subscribe((response) => {
    // //     if (response) {
    // //       this.roles = response;
    // //     }
    // //   });

    this.getAllUsers(this.curPage);
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.resetSearch();
    });

    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getAllUsers(pageNumber: number) {
    // if (!isSearch) this.clearForm();
    let searchBody: IUserSearchVM = {
      ar_name: this.FormCtrls.userName.value?.trim().toLowerCase(),
      account_email: this.FormCtrls.email.value?.trim().toLowerCase(),
      mobile: this.FormCtrls.phone.value?.trim(),
      code: this.FormCtrls.code.value?.trim().toLowerCase(),
    };

    this.itemsCounts = 0;
    this.curPage = pageNumber;
    this.spinner.show();
    this.userService
      .getAllUsers<IUserViewModel>(this.EduCompId, pageNumber, searchBody)
      .subscribe((response) => {
        if (response) {
          // console.log("users: ", response);
          this.dataSource = new MatTableDataSource(response.usersList);
          this.itemsCounts = response.countItems;
          this.dataSource.sort = this.sort;
        }
        this.spinner.hide();
      });
  }

  editUser(user: IUserModel) {
    // this.myForm.patchValue(user);
    this.FormCtrls.userId?.setValue(user.user_ID);
    this.FormCtrls.userName?.setValue(user.ar_name);
    this.FormCtrls.email?.setValue(user.account_email);
    this.FormCtrls.phone?.setValue(user.mobile);
    this.FormCtrls.code?.setValue(user.code);
    // this.FormCtrls.roleId?.setValue(user.roleId);
    // this.FormCtrls.password?.setValue(user.account_password);
    // this.FormCtrls.confirmedPass?.setValue(user.account_password);
  }

  removeUser(user: IUserModel) {
    this.spinner.show();
    this.userService.deleteUser(user.user_ID).subscribe((response: any) => {
      if (response && response?.returnValue == 1) {
        // console.log("user: ", response);
        this.toastr.success("General.DeleteSuccessfully");
        if (user.user_ID == this.FormCtrls.userId.value) this.clearForm();
        if (this.dataSource.data.length == 1 && this.curPage > 1) {
          // if last item in list: get previous page
          this.getAllUsers(this.curPage - 1);
          this.paginator.pageIndex = this.curPage - 1;
        } else {
          // if not last item in list: get current page
          this.getAllUsers(this.curPage);
        }
      } else this.toastr.error(response?.returnString);
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      this.spinner.show();
      let obj = {} as IAddEditUserModel;
      obj.user = {} as IUserModel;
      obj.user.user_ID = this.FormCtrls.userId?.value;
      obj.user.ar_name = this.FormCtrls.userName?.value;
      obj.user.account_email = this.FormCtrls?.email?.value;
      obj.user.mobile = this.FormCtrls.phone?.value;
      // obj.user.roleId = this.FormCtrls.roleId?.value;
      obj.user.account_password = this.FormCtrls.password?.value;
      obj.code = this.FormCtrls.code?.value;
      // obj.roleId = this.FormCtrls.roleId?.value;
      obj.EduCompId = this.EduCompId;

      this.userService
        .addEditUser<IAddEditUserModel, any>(obj)
        .subscribe((response) => {
          // console.log("created: ", response);
          if (response && response !== null) {
            if (response.returnValue == 0) {
              this.toastr.error(response.returnString);
            } else {
              this.clearForm();

              if (obj.user.user_ID == 0) {
                this.getAllUsers(1); // first page after add
                this.dataSource.paginator = this.paginator;
              } else {
                this.getAllUsers(this.curPage); // current page after update
              }

              this.toastr.success(
                obj.user.user_ID == 0
                  ? "General.AddSuccessfully"
                  : "General.UpdateSuccessfully"
              );
            }
          }
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }

  openDeleateDialog(user: IUserModel): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: user.ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeUser(user);
      });
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.userId.setValue(0);
    this.submitted = false;
  }

  resetSearch() {
    this.clearForm();
    this.getAllUsers(1);
    this.dataSource.paginator = this.paginator;
  }
}
