import { LanguageService } from "app/shared/services/language.service";
import {
  IDepartmentModel,
  IDepartmentViewModel,
} from "app/admin/models/setup/department";
import { Component, OnInit, ViewChild, ElementRef, Input } from "@angular/core";
import { DepartmentService } from "app/admin/services/setup/department.service";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { NgxSpinnerService } from "ngx-spinner";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { AuthService } from "app/shared/services/auth/auth.service";
import { ActivatedRoute } from "@angular/router";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { MessagesService } from "app/shared/services/messages.service";

@Component({
  selector: "app-department",
  templateUrl: "./department.component.html",
  styleUrls: ["./department.component.scss"],
})
export class DepartmentComponent implements OnInit {
  @Input() showAddOnly: boolean = false;
  departments: IDepartmentModel[];
  myForm: FormGroup;
  displayedColumns: string[] = ["#", "en_name", "ar_name", "action"];
  dataSource: MatTableDataSource<IDepartmentModel>;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  itemsCounts: number = 0;
  curPage: number = 1;
  submitted: boolean = false;
  // @ViewChild("addBtn", { read: ElementRef }) addBtn: ElementRef;
  functionId: number;
  EduCompId: number;
  rowFunctions: IRowFunctionVM[];

  constructor(
    private departmentService: DepartmentService,
    private fb: FormBuilder,
    private toastr: MessagesService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authService: AuthService,
    public languageService: LanguageService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllDepartments(this.curPage);

    this.myForm = this.fb.group({
      id: [0],
      ar_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      en_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllDepartments(1);
      this.dataSource.paginator = this.paginator;
    });

    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getAllDepartments(page: number) {
    this.curPage = page;
    this.spinner.show();
    this.departmentService
      .getAllDepartments<IDepartmentViewModel>(page, this.EduCompId)
      .subscribe((res) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.depratmentsList);
          this.itemsCounts = res.countItems;
          this.dataSource.sort = this.sort;
        }
        this.spinner.hide();
      });
  }

  onSubmit() {
    this.submitted = true;

    if (this.myForm.valid) {
      let data = {} as IDepartmentModel;
      data.id = this.FormCtrls.id.value;
      data.ar_name = this.FormCtrls.ar_name.value;
      data.en_name = this.FormCtrls.en_name.value;
      data.EduCompId = this.EduCompId;

      this.spinner.show();
      if (data.id == 0) this.addDepartment(data);
      else this.updateDepartment(data, data.id);
    } else this.toastr.error("General.FormNotValid");
  }

  addDepartment(data: IDepartmentModel) {
    this.departmentService.addDepartment(data).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.getAllDepartments(1); // first page after add
        this.dataSource.paginator = this.paginator;
        this.toastr.success("General.AddSuccessfully");
        this.clearForm();
      } else this.toastr.error(res.returnString);
      this.spinner.hide();
    });
  }

  updateDepartment(data: IDepartmentModel, id: number) {
    this.departmentService.editDepartment(data, id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.getAllDepartments(this.curPage); // current page after update
        this.toastr.success("General.UpdateSuccessfully");
        this.clearForm();
      } else this.toastr.error(res.returnString);
      this.spinner.hide();
    });
  }

  //start edit
  getDepartmentById(department: IDepartmentModel) {
    this.myForm.patchValue(department);
  }

  // start delete
  deleteDepartment(department: IDepartmentModel) {
    this.spinner.show();
    this.departmentService
      .deleteDepartment(department.id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.toastr.success("General.DeleteSuccessfully");
          if (this.dataSource.data.length == 1 && this.curPage > 1) {
            // if last item in list: get previous page
            this.getAllDepartments(this.curPage - 1);
            this.paginator.pageIndex = this.curPage - 1;
          } else {
            // if not last item in list: get current page
            this.getAllDepartments(this.curPage);
          }
          if (department.id == this.FormCtrls.id.value) this.clearForm();
        } else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
  }

  openDeleateDialog(department: IDepartmentModel): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.languageService.isArabic
            ? department.ar_name ?? department.en_name
            : department.en_name ?? department.ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteDepartment(department);
      });
  }
  // end delete

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
  }
}
