import { ActivatedRoute } from "@angular/router";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { MatDialog } from "@angular/material/dialog";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import {
  Category,
  CategoriesViewModel,
  IAddCategoryVM,
} from "app/admin/models/setup/categories";
import { CategoriesService } from "app/admin/services/setup/categories.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { NgxSpinnerService } from "ngx-spinner";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { AppAssets } from "app/shared/data/app-assets";

@Component({
  selector: "app-categorie",
  templateUrl: "./categorie.component.html",
  styleUrls: ["./categorie.component.scss"],
})
export class CategorieComponent implements OnInit {
  inputForm!: FormGroup;
  submitted: boolean = false;
  displayedColumns: string[] = [
    "id",
    "en_name",
    "ar_name",
    "attach_path",
    "options",
  ];
  dataSource: MatTableDataSource<Category>;
  itemsCounts: number = 0;
  curPage: number = 1;
  categories: Category[] = [];
  functionId: number;
  EduCompId: number;
  rowFunctions: IRowFunctionVM[];
  imageChange: boolean = false;
  // removeImage: number = 0;

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  constructor(
    private fb: FormBuilder,
    private _serviceCategories: CategoriesService,
    private toastr: MessagesService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authService: AuthService,
    public languageService: LanguageService,
    public fileService: UploadFileService,
    public appAssets: AppAssets
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });
    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.getCategories(this.curPage);

    this.inputForm = this.fb.group({
      id: [0],
      ar_name: [null, [Validators.required, CustomeValidator.whiteSpace]],
      en_name: [null, [Validators.required, CustomeValidator.whiteSpace]],
      category_attach: [null /* , [Validators.required] */],
      attach_url: [null],
    });

    this.FormCtrls.category_attach.valueChanges.subscribe((attach) => {
      if (attach) {
        // this.removeImage = 0;
        this.imageChange = true;
      }
    });
  }

  get FormCtrls() {
    return this.inputForm.controls;
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getCategories(1);
      this.dataSource.paginator = this.paginator;
    });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getCategories(page: number) {
    this.curPage = page;
    this.spinner.show();
    this._serviceCategories
      .getCategories<CategoriesViewModel>(page)
      .subscribe((data) => {
        if (data) {
          this.dataSource = new MatTableDataSource(data.courseCategoryList);
          this.itemsCounts = data.countItems;
          this.dataSource.sort = this.sort;
        }
        this.spinner.hide();
      });
  }

  addCategories() {
    this.submitted = true;
    if (this.inputForm.valid) {
      const model: IAddCategoryVM = {
        id: this.inputForm.value.id,
        EduCompId: this.EduCompId,
        ar_name: this.inputForm.value.ar_name,
        en_name: this.inputForm.value.en_name,
        categoryAttach: this.FormCtrls.category_attach.value,
        // remove_image: this.removeImage,
      };

      this.spinner.show();
      this._serviceCategories.addCategories(model).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.clearForm();
          this.getCategories(1); // first page after add
          this.dataSource.paginator = this.paginator;
          this.toastr.success("General.AddSuccessfully");
        } else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
    } else this.toastr.error("General.FormNotValid");
  }

  updateCategories() {
    this.submitted = true;
    if (this.inputForm.valid) {
      const model: IAddCategoryVM = {
        id: this.inputForm.value.id,
        EduCompId: this.EduCompId,
        ar_name: this.inputForm.value.ar_name,
        en_name: this.inputForm.value.en_name,
        categoryAttach: this.FormCtrls.category_attach.value,
      };
      this.spinner.show();
      this._serviceCategories
        .editCategories(model, model.id)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            this.clearForm();
            this.getCategories(this.curPage); // current page after update
            this.toastr.success("General.UpdateSuccessfully");
          } else this.toastr.error(res.returnString);
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }

  edit(category: Category) {
    this.inputForm.patchValue(category);
    this.FormCtrls.category_attach.setValue(category.attach_path);
  }

  deleteCategories(category: Category) {
    this.spinner.show();
    this._serviceCategories
      .deleteCategories(category.id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.toastr.success("General.DeleteSuccessfully");
          if (this.dataSource.data.length == 1 && this.curPage > 1) {
            // if last item in list: get previous page
            this.getCategories(this.curPage - 1);
            this.paginator.pageIndex = this.curPage - 1;
          } else {
            // if not last item in list: get current page
            this.getCategories(this.curPage);
          }
          if (category.id == this.FormCtrls.id.value) this.clearForm();
        } else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
  }

  openDeleateDialog(category: Category): void {
    // console.log(category);
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.languageService.isArabic
            ? category.ar_name ?? category.en_name
            : category.en_name ?? category.ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteCategories(category);
      });
  }

  clearForm() {
    this.inputForm.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
  }

  // RemoveFile() {
  //   this.removeImage = 1;
  //   this.FormCtrls.category_attach.setValue(null);
  // }
}
