import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class WhatsAppService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  sendSingularWhatsAppMsg(data: any) {
    return this.post(`whats-app/send-singular-message`, data);
  }

  sendWhatsAppMsg(data: any) {
    return this.post(`whats-app/send-message`, data);
  }
}
