import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class FunctionsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  addEditFunctionURL = "usermanagement/AddEditFunction";
  deleteFunctionURL = "usermanagement/deleteFunction";

  addEditFunction<T, TV>(obj: T) {
    return this.post<TV>(this.addEditFunctionURL, obj);
  }
  //   {
  //     "Id":0,
  //     "functionName":"test ",
  //     "function_en_name":"test Eng",
  //     "functionURL":"url test"
  // }

  deleteFunction<T>(functionId: number) {
    return this.delete<T>(this.deleteFunctionURL + "/" + functionId);
  }
}
