import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class BranchesService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getAllBranchesURL = "usermanagement/returnCenterBranchesByEduComp/";
  addEditBranchesURL = "usermanagement/AddEditBranch";
  deleteBranchesURL = "usermanagement/deleteBranch";

  getAllDistrictsURL = "usermanagement/returnDistrictsList"; // DropDownMenu
  getAllAllCenterBranchesURL = "usermanagement/returnAllCenterBranches"; // /{page} => paginated

  getAllBranches<T>(eduCompId: any) {
    return this.get<T>(this.getAllBranchesURL + "/" + eduCompId);
  }

  getAllAllCenterBranches<T>(eduCompId: number, page: number) {
    return this.get<T>(
      this.getAllAllCenterBranchesURL + "/" + eduCompId + "/" + page
    );
  }

  addEditBranches<T, TV>(obj: T) {
    return this.post<TV>(this.addEditBranchesURL, obj);
  }
  //   {
  //     "Id":18,
  //     "EduCompId":2,
  //     "Name":"Test edit Branch",
  //     "districtId":2
  // }

  deleteBranch<T>(branchId: number) {
    return this.delete<T>(this.deleteBranchesURL + "/" + branchId);
  }

  getAllDistricts<T>() {
    return this.get<T>(this.getAllDistrictsURL);
  }
}
