import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class ExamsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getTeachersByEducompId(EduCompId: any) {
    return this.get(`Admin/QuestionsBank/${EduCompId}`);
  }

  getQuestions(page: any, EduCompId: any, data: any) {
    return this.post(`Admin/QuestionsBankTable/${page}/${EduCompId}`, data);
  }

  getQuestionByID(id: any) {
    return this.get(`Admin/get-question-bank/${id}`);
  }

  addEditQuestion(eduCompID: any, data: any) {
    return this.post(`Admin/question-bank-add-edit/${eduCompID}`, data);
  }

  updateQuestion(Id: any, data: any) {
    return this.put(`Admin/QuestionsBank/${Id}`, data);
  }

  deleteQuestion(eduCompID: any, id: any) {
    return this.post(`Admin/removeQuestionBank/${eduCompID}/${id}`, null);
  }

  getSubjectsByTeacherId(teacherId: any) {
    return this.post(`Admin/get-lectures-by-teacher-query`, teacherId);
  }

  getAllNormalExams<T, TV>(obj: T) {
    return this.post<TV>("exam/returnRecentlyAddedExams", obj);
  }

  publishExam<TV>(examId: number) {
    return this.get<TV>(`exam/publish-depublish-exam/${examId}`);
  }

  deleteExam<TV>(id: number) {
    // console.log(id);
    return this.delete<TV>(`Admin/RemoveExam/${id}`);
  }

  getAllCountries<T>() {
    return this.get<T>("country/return-countries");
  }

  getAllStages<T>(countryId: number) {
    return this.get<T>(`stage/return-stages/${countryId}`);
  }

  getAllEducationYears<T>(stageId: number) {
    return this.get<T>(`year/return-years/${stageId}`);
  }

  getAllSubjects<T>(educationYearId: number) {
    return this.get<T>(`subject/return-subjects/${educationYearId}`);
  }

  getAllTeachersBySublectId<T>(subjectId: number) {
    return this.get<T>(`teacher/return-teachers/${subjectId}`);
  }

  getAllUnitsBySublectId<T>(subjectId: number) {
    return this.get<T>(`unit/return-units/${subjectId}`);
  }

  getAllLessonsByUnitId<T>(unitId: number) {
    return this.get<T>(`lesson/return-lesson/${unitId}`);
  }

  getAllExamType<T>() {
    return this.get<T>("exam/return-exam-types");
  }

  getExamById<T>(id: number) {
    return this.get<T>(`exam/get-exam-by-id/${id}`);
  }

  getExamDetailsById<T>(examId: number) {
    return this.get<T>(`Exam/Details/${examId}`);
  }

  addExam<T, TY>(data: T) {
    return this.post<TY>("exam/add-exam", data);
  }

  getAllQuestionGroups<T>() {
    return this.get<T[]>("exam/return-question-groups");
  }

  getAllQuestionHeaders<T>() {
    return this.get<T[]>("exam/return-question-heads");
  }

  addExamGroupHeader<T, TY>(data: T) {
    return this.post<TY>(`exam/add-question-group-head`, data);
  }

  editExamGroupHeader<T, TY>(data: T) {
    return this.post<TY>("exam/EditQuestionHead", data);
  }

  deleteExamGroupHeader(id: number) {
    return this.delete(`exam/deleteExamHeadGroupById/${id}`);
  }

  getAllGroupHeaderByExamId<T>(id: number) {
    return this.get<T[]>(`exam/GetQuestionHeadGroupByExamId/${id}`);
  }

  getAllQuestionsByHeadId<T>(id: number) {
    return this.get<T>(`exam/GetExamQuestionsByQuestionHead/${id}`);
  }

  addQuestion<T>(data: any) {
    return this.post<T>("exam/add-exam-questions", data);
  }

  addSurveyQuestion<T>(data: any) {
    return this.post<T>("exam/add-survey-questions", data);
  }

  getQuestionForEditByID<T>(id: number) {
    return this.get<T>(`exam/return-question-details/${id}`);
  }

  editQuestion<T>(data: any) {
    return this.post<T>("exam/edit-exam-questions", data);
  }

  editSurveyQuestion<T>(data: any) {
    return this.post<T>("exam/edit-survey-questions", data);
  }

  deleteExamQuestion<T>(id: number) {
    // console.log(id);
    return this.delete<T>(`exam/RemoveExamQuestion/${id}`);
  }

  getExamPoints(examId: any, page: any) {
    return this.get(`exam/returnCustomPointExamByExamId/${examId}/${page}`);
  }

  addExamPoint(body: any) {
    return this.post("exam/CustomExamPoint", body);
  }

  deleteExamPoint(pointId: any) {
    return this.delete(`exam/DeleteCustomExamPoint/${pointId}`);
  }

  UploadExamStudentGrades(data: any) {
    return this.post("Exam/postStudentsGrades", data);
  }

  getQuestionByCorrectionType(page: any, correctionStatus: any) {
    return this.get(`Exam/CorrectionIndexPaging/${page}/${correctionStatus}`);
  }

  submitCorrection(body: any) {
    return this.post(`/Exam/submitQuestionCorrection`, body);
  }
}
