import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class PointsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  //  Points Setup

  addPoints(educompId: number, data: any) {
    return this.post(`SetupPoints/SetupPoints/${educompId}`, data);
  }

  getPoints<T>(courseId: number, typeId: number, eduCompId: number) {
    return this.get<T>(
      `SetupPoints/returnPointSystemByResourceId/${courseId}/${typeId}/${eduCompId}`
    );
  }

  deletePoint(pointId: any) {
    return this.post(`SetupPoints/DeletePointSystemDetails/${pointId}`, null);
  }

  //######################################################################
  // Custome
  //######################################################################
  // Lecture Custome

  addLectureCustomPoint(data: any) {
    return this.post(`SetupPoints/CustomMaterialPoint`, data);
  }

  getLectureCustomPoints<T>(page: any, materialId: any) {
    return this.get<T>(
      `SetupPoints/returnCustomPointMaterialByMaterialId/${materialId}/${page}`
    );
  }

  deleteLectureCustomPoint(pointId: any) {
    return this.post(`SetupPoints/DeleteCustomMaterialPoint/${pointId}`, null);
  }

  //######################################################################
  // Exam Custome
  addExamCustomPoint(body: any) {
    return this.post("SetupPoints/CustomExamPoint", body);
  }

  getExamCustomPoints<T>(page: any, examId: any) {
    return this.get<T>(
      `SetupPoints/returnCustomPointExamByExamId/${examId}/${page}`
    );
  }

  deleteExamCustomPoint(pointId: any) {
    return this.post(`SetupPoints/DeleteCustomExamPoint/${pointId}`, null);
  }

  //######################################################################
  // Template Custome
  addTemplateCustomPoint(body: any) {
    return this.post("SetupPoints/CustomTemplatePoint", body);
  }

  getTemplateCustomPoints<T>(page: any, templateId: any) {
    return this.get<T>(
      `SetupPoints/returnCustomPointTemplateByTemplateId/${templateId}/${page}`
    );
  }
}
