import { Component, OnInit, ViewChild } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { MessagesService } from "app/shared/services/messages.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatSort } from "@angular/material/sort";
import { MatPaginator } from "@angular/material/paginator";
import { MatTableDataSource } from "@angular/material/table";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";
import {
  ICourseObjectiveAddEditVM,
  ICourseObjectiveVM,
} from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { LanguageService } from "app/shared/services/language.service";

@Component({
  selector: "app-course-objectives",
  templateUrl: "./course-objectives.component.html",
  styleUrls: ["./course-objectives.component.scss"],
})
export class CourseObjectivesComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  curPage: number = 1;
  courseId: number = 0;

  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];

  displayColumns: string[] = ["#", "whatYouLearn", "actions"];
  dataSource: MatTableDataSource<ICourseObjectiveVM>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    public languageService: LanguageService,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    public authService: AuthService,
    private courseService: CourseService
  ) {
    this.route.paramMap.subscribe(
      (pram) => (this.courseId = Number(pram.get("courseId")))
    );

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: IRowFunctionVM[]) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      subjectObjectiveId: [0],
      whatYouLearn: ["", [Validators.required, CustomeValidator.whiteSpace]],
    });

    this.getAllCourseObjectives(this.curPage);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      // this.getAllCourseObjectives(1);
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  editCourseObjective(courseObjective: ICourseObjectiveAddEditVM) {
    this.myForm.patchValue(courseObjective);
  }

  // Return Course Objective
  getAllCourseObjectives(page: number) {
    this.spinner.show();
    this.courseService
      .getAllCourseObjectives(this.courseId, page)
      .subscribe((res) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.courseList);
          this.itemsCounts = res.countItems;
          this.dataSource.sort = this.sort;
        }
        this.spinner.hide();
      });
  }

  onSubmit() {
    this.submitted = true;

    if (this.myForm.valid) {
      let courseObjective: ICourseObjectiveAddEditVM = {
        subject_id: this.courseId,
        id: this.FormCtrls.subjectObjectiveId.value,
        what_learn: this.FormCtrls.whatYouLearn.value,
      };

      this.spinner.show();
      this.courseService
        .addEditCourseObjective(courseObjective)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            if (courseObjective.id == 0) {
              this.getAllCourseObjectives(1); // first page after add
              this.dataSource.paginator = this.paginator;
            } else {
              this.getAllCourseObjectives(this.curPage); // current page after update
            }

            this.toastr.success(
              courseObjective.id == 0
                ? "General.AddSuccessfully"
                : "General.UpdateSuccessfully"
            );

            this.clearForm();
          } else this.toastr.error(res?.returnString);
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }

  openDeleteDialog(courseObjective: ICourseObjectiveVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: courseObjective.subjectObjectiveId,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteCourseObjective(courseObjective);
      });
  }

  // Delete Course Objective
  deleteCourseObjective(courseObjective: ICourseObjectiveVM) {
    this.spinner.show();
    this.courseService
      .deleteCourseObjective(courseObjective.subjectObjectiveId)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.toastr.success("General.DeleteSuccessfully");

          if (this.dataSource.data.length == 1 && this.curPage > 1) {
            // if last item in list: get previous page
            this.getAllCourseObjectives(this.curPage - 1);
            this.paginator.pageIndex = this.curPage - 1;
          } else {
            // if not last item in list: get current page
            this.getAllCourseObjectives(this.curPage);
          }

          if (
            courseObjective.subjectObjectiveId ==
            this.FormCtrls.subjectObjectiveId.value
          )
            this.clearForm();
        } else this.toastr.error(res?.returnString);
        this.spinner.hide();
      });
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.subjectObjectiveId.setValue(0);
    this.submitted = false;
  }
}
