import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class CashPaymentService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  CashPaymentSearch(body: any, EduCompId: any) {
    return this.post(
      `CashPayment/returnUserDataByPhoneNumber/${EduCompId}`,
      body
    );
  }

  CashPaymentPost(EduCompId: any, body: any) {
    return this.post(`CashPayment/CashPaymentPost/${EduCompId}`, body);
  }

  GetItemsByLesson(
    EduCompId: number,
    lessonId: number,
    itemTypeId: number,
    teacherId: number
  ) {
    return this.get(
      `${`CashPayment/returnItemsByLessonId`}/${EduCompId}/${lessonId}/${itemTypeId}/${teacherId}`
    );
  }

  GetItemsByUnit(
    EduCompId: number,
    unitId: number,
    itemTypeId: number,
    teacherId: number
  ) {
    return this.get(
      `CashPayment/returnItemsByUnitId/${EduCompId}/${unitId}/${itemTypeId}/${teacherId}`
    );
  }

  GetItemsBySubject(
    EduCompId: number,
    subjectId: number,
    itemTypeId: number,
    teacherId: number
  ) {
    return this.get(
      `CashPayment/returnItemsBySubjectId/${EduCompId}/${subjectId}/${itemTypeId}/${teacherId}`
    );
  }

  GetItemsByLessonWithEnded(
    EduCompId: number,
    lessonId: number,
    itemTypeId: number,
    teacherId: number,
    ended: boolean
  ) {
    return this.get(
      `CashPayment/returnItemsByLessonIdSpecialViews/${EduCompId}/${lessonId}/${itemTypeId}/${teacherId}/${ended}`
    );
  }

  geItemPrice(id: number, userId: number) {
    return this.get(`CashPayment/returnMaterialPriceById/${id}/${userId}`);
  }

  getPaymentsHistory(p: number, userId: any) {
    return this.get(
      `CashPayment/returnUserPaymentsHistoryTable/${p}/${userId}`
    );
  }

  ManualPaymentByExcel(EduCompId: number, body: any) {
    return this.post(`CashPayment/postManualPaymentByExcel/${EduCompId}`, body);
  }
}
