import { LanguageService } from "app/shared/services/language.service";
import { DatePipe } from "@angular/common";
import { Component, OnInit, ViewChild } from "@angular/core";
import {
  AbstractControlOptions,
  FormBuilder,
  FormGroup,
  Validators,
} from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import {
  GetJobDepartment,
  JobTitleByDepartment,
  JopDepViewModel,
} from "app/admin/models/setup/job-title-by-department";
import { JopTitleDepartmentService } from "app/admin/services/setup/job-title-department.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { UpdateDateComponent } from "app/shared/components/dialogs/update-date/update-date.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-deparment-jop-title",
  templateUrl: "./deparment-job-title.component.html",
  styleUrls: ["./deparment-job-title.component.scss"],
})
export class UserDeparmentAndJobTitleComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  alljoblist: GetJobDepartment[] = [];

  displayedColumns: string[] = [
    "id",
    "dept_en_name",
    "dept_ar_name",
    "JobTitle",
    "options",
  ];
  dataSource: MatTableDataSource<JobTitleByDepartment>;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  currentDate = new Date();

  functionId: number;
  EduCompId: number;
  functionParentId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions = [
  //   { functionName: "Edit" },
  //   { functionName: "Delete" },
  //   { functionName: "UpdateArticalJob" },
  //   { functionName: "DeleteArticalJob" },
  // ];
  userId: number;
  username: string;
  jopTitle = [];
  dep_id: number = 0;
  curPage: number = 1;
  initTable: boolean = true;
  deptsForUser: JobTitleByDepartment[];

  constructor(
    private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    private jopTitleDepartmentService: JopTitleDepartmentService,
    private toastr: MessagesService,
    private route: ActivatedRoute,
    private dialog: MatDialog,
    public authserv: AuthService,
    private datePipe: DatePipe,
    public languageService: LanguageService
  ) {
    //get userid
    route.paramMap.subscribe((params) => {
      this.userId = Number(params.get("userId"));
    });

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      this.functionParentId = Number(params.get("parentId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              // console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getall(1);

    this.myForm = this.fb.group(
      {
        dept_id: [0, [Validators.required]],
        job_departments: [
          {
            value: "",
            disabled: true,
          },
          [Validators.required],
        ],
        from_date: [null],
        to_date: [null],
      },
      {
        validator: CustomeValidator.dateBefore("from_date", "to_date"),
      } as AbstractControlOptions
    );
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
    });
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  applyFilter(e: string) {
    this.dataSource.filter = e.trim().toLowerCase();
  }

  customFilter() {
    this.dataSource.filterPredicate = (data: JobTitleByDepartment, filter) => {
      return (
        data.dept_ar_name?.toLocaleLowerCase().indexOf(filter) != -1 ||
        data.dept_en_name?.toLocaleLowerCase().indexOf(filter) != -1 ||
        data.job_departments
          ?.map((d) => d.job_ar_name)
          .join(" ")
          .toLocaleLowerCase()
          .indexOf(filter) != -1 ||
        data.job_departments
          ?.map((d) => d.job_en_name)
          .join(" ")
          .toLocaleLowerCase()
          .indexOf(filter) != -1
      );
    };
  }

  getall(page: number) {
    this.curPage = page;
    this.spinner.show();
    return this.jopTitleDepartmentService
      .getJopTitlebyDepartment<JopDepViewModel>(this.userId, page)
      .subscribe((data) => {
        if (data) {
          this.dataSource = new MatTableDataSource(
            data.userDepartmentJobtitleList
          );
          this.itemsCounts = data.itemsCounts;
          this.deptsForUser = data.userDepartmentJobtitleList;
          this.username = data.employee_name;
          // console.log(this.deptsForUser)
          this.startSortingTable();
          this.customFilter();
        }
        this.spinner.hide();
      });
  }

  startSortingTable() {
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "JobTitle":
          return this.languageService.isArabic
            ? item.job_departments[0]?.job_en_name
            : item.job_departments[0]?.job_en_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  addAll() {
    this.submitted = true;
    // console.log("ff", model);
    if (this.myForm.valid) {
      const model = {
        user_id: this.userId,
        jobTitle_dept_id: this.formCtrls["job_departments"].value,
        from_date: this.datePipe.transform(
          this.myForm.value.from_date ?? this.currentDate
        ),
        to_date: this.datePipe.transform(this.myForm.value.to_date),
      };

      this.spinner.show();
      this.jopTitleDepartmentService
        .addJopTitlebyDepartment(model)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            // console.log("add", model);
            this.getall(1); // first page after add
            this.dataSource.paginator = this.paginator;
            this.toastr.success("General.AddSuccessfully");
            this.clearForm();
            // console.log(this.myForm.value);
          } else this.toastr.error(res.returnString);
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }

  updetAll() {
    this.submitted = true;

    if (this.myForm.valid) {
      let allDepJobIds: number[] = this.formCtrls["job_departments"].value;
      this.deptsForUser.forEach((dep) => {
        if (dep.dept_id != this.dep_id) {
          dep.job_departments.forEach((job) =>
            allDepJobIds.push(job.dep_job_id)
          );
        }
      });
      // console.log(allDepJobIds)
      const model = {
        user_id: this.userId,
        jobTitle_dept_id: allDepJobIds,
        from_date: this.datePipe.transform(
          this.myForm.value.from_date ?? this.currentDate
        ),
        to_date: this.datePipe.transform(this.myForm.value.to_date),
      };

      this.spinner.show();
      this.jopTitleDepartmentService
        .editAllDeparment(model, this.formCtrls["dept_id"].value)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            this.getall(this.curPage);
            this.toastr.success("General.UpdateSuccessfully");
            this.clearForm();
            this.dep_id = 0;
          } else this.toastr.error(res.returnString);
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }

  editAll(department: JobTitleByDepartment) {
    //  this.myForm.patchValue(department);
    this.dep_id = department.dept_id;
    this.formCtrls["dept_id"].setValue(department.dept_id);
    this.formCtrls["job_departments"]?.setValue(
      department.job_departments.map((j) => j.dep_job_id)
    );
  }

  editDate(editjobTitle: GetJobDepartment) {
    this.dialog
      .open(UpdateDateComponent, {
        data: {
          user_dept_job_id: editjobTitle.user_dept_job_id,
          from_date: editjobTitle.from_date,
          to_date: editjobTitle.to_date,
        },
      })
      .afterClosed()
      .subscribe((res) => {
        console.log(res);
        if (res) this.getall(this.curPage);
      });
  }

  deleteUserDeptJobTitle(depId: JobTitleByDepartment) {
    this.spinner.show();
    this.jopTitleDepartmentService
      .deleteUserDeptJobTitle(this.userId, depId.dept_id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.toastr.success("General.DeleteSuccessfully");
          if (depId.dept_id == this.formCtrls.dept_id.value) this.clearForm();
          if (this.dataSource.data.length == 1 && this.curPage > 1) {
            // if last item in list: get previous page
            this.getall(this.curPage - 1);
            this.paginator.pageIndex = this.curPage - 1;
          } else {
            // if not last item in list: get current page
            this.getall(this.curPage);
          }
        } else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
  }

  openDeleateDialog(depId: JobTitleByDepartment): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.languageService.isArabic
            ? depId.dept_en_name
            : depId.dept_ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteUserDeptJobTitle(depId);
      });
  }

  deleteJopTitle(jopid: GetJobDepartment) {
    this.spinner.show();
    this.jopTitleDepartmentService
      .deleteJopTitle(jopid.user_dept_job_id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.toastr.success("General.DeleteSuccessfully");
          this.getall(this.curPage);
        } else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
  }

  openDeleateDialogJop(jopid: GetJobDepartment): void {
    // console.log("id", jopid.user_dept_job_id);
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.languageService.isArabic
            ? jopid.job_ar_name
            : jopid.job_en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteJopTitle(jopid);
      });
  }

  clearForm() {
    this.myForm.reset();
    this.formCtrls["dept_id"]?.setValue(0);
    this.dep_id = 0;
    this.submitted = false;
  }
}
