import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { IUserSearchVM } from "app/admin/models/roles/user";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class UsersService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getAllUsers<T>(eduCompId: any, page: number, searchBody: IUserSearchVM) {
    return this.post<T>(
      `usermanagement/returnUsersByEduComp/${eduCompId}/${page}`,
      searchBody
    );
  }

  addEditUser<T, TV>(obj: T) {
    return this.post<TV>("usermanagement/create-user-account", obj);
  }

  deleteUser(userId: number) {
    return this.delete(`usermanagement/deleteUser/${userId}`);
  }

  getAllBranchesByCenter<T>(eduCompId: any) {
    return this.get<T>(
      `usermanagement/returnCenterBranchesByEduComp/${eduCompId}`
    );
  }
}
