import { Component, OnInit, ViewChild } from "@angular/core";
import {
  AbstractControlOptions,
  FormBuilder,
  FormGroup,
  Validators,
} from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";

import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { AuthService } from "app/shared/services/auth/auth.service";
import {
  ISearchDiploma,
  IDiplomaAddEditVM,
  IDiplomaGetModel,
  IDiplomaGetVM,
} from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { MessagesService } from "app/shared/services/messages.service";
import { LanguageService } from "app/shared/services/language.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { Category } from "app/admin/models/setup/categories";
import { CategoriesService } from "app/admin/services/setup/categories.service";

@Component({
  selector: "app-diploma",
  templateUrl: "./diploma.component.html",
  styleUrls: ["./diploma.component.scss"],
})
export class DiplomaComponent implements OnInit {
  totalItems: number;
  DiplomaId: number = 0;

  displayedColumns: string[] = [
    "#",
    "diploma_en_name",
    "diploma_ar_name",
    "categories",
    "price",
    "from_date",
    "to_date",
    "diploma_description",
    "diploma_attach",
    "actions",
  ];
  dataSource: MatTableDataSource<IDiplomaGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  myForm!: FormGroup;
  curPage: number = 1;
  submitted: boolean = false;
  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  categories: Category[];

  constructor(
    private courseServ: CourseService,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private authserv: AuthService,
    private spinner: NgxSpinnerService,
    private msg: MessagesService,
    private dialog: MatDialog,
    private languageService: LanguageService,
    public fileService: UploadFileService,
    private categoriesService: CategoriesService
  ) {
    this.route.data.subscribe((val) => {
      console.log("view:-", val);
    });
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log(`RowFunctions for"${this.functionId}": `, res);
              this.rowFunctions = res;
            }
          });
      }
    });
  }

  ngOnInit(): void {
    this.EduCompId = Number(this.authserv.getEduCompId());

    this.myForm = this.fb.group(
      {
        diploma_ar_name: [null, [Validators.required]],
        diploma_en_name: [null, [Validators.required]],
        price: [null, [Validators.required, CustomeValidator.minusNum]],
        from_date: [null],
        to_date: [null],
        diploma_description: [null],
        diploma_attach: [null],
        categoryIds: [null, [Validators.required]],

        // main_coursId: [null, [Validators.required]],
        // eduCompId: [this.eduCompId],
      },
      {
        validator: CustomeValidator.dateBefore("from_date", "to_date"),
      } as AbstractControlOptions
    );
    this.getAllCategories();

    // initialize dataSource | get all courses
    this.getAllDiplomas(this.curPage, false);
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllDiplomas(1, false);
      this.dataSource.paginator = this.paginator;
    });

    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  customFilter() {
    this.dataSource.filterPredicate = (data: IDiplomaGetVM, filter) => {
      return (
        data.diploma_ar_name?.toLocaleLowerCase().indexOf(filter) != -1 ||
        data.diploma_en_name?.toLocaleLowerCase().indexOf(filter) != -1
      );
    };
  }

  getAllCategories() {
    this.categoriesService.getCategoriesDropdown().subscribe((res) => {
      if (res) this.categories = res.courseCategoryList;
    });
  }
  getAllDiplomas(p: number, search: boolean) {
    this.curPage = p;

    let searchData: ISearchDiploma;
    if (search) {
      // get by search
      searchData = {
        // diploma_ar_name:
        //   this.FormCtrls.diploma_ar_name?.value?.trim() || null,
        // diploma_en_name:
        //   this.FormCtrls.diploma_en_name?.value?.trim() || null,
        diplomaName:
          this.FormCtrls.diploma_en_name?.value?.trim().toLowerCase() ||
          this.FormCtrls.diploma_ar_name?.value?.trim().toLowerCase() ||
          null,
        page: this.curPage,
        eduCompId: this.EduCompId || 0,
      };
    } else {
      searchData = {
        // diploma_ar_name: null,
        // diploma_en_name: null,
        diplomaName: null,
        page: this.curPage,
        eduCompId: this.EduCompId || 0,
      };
    }

    this.spinner.show();
    this.courseServ
      // .getAllDiplomas<IDiplomaGetModel>(this.curPage)
      .getAllDiplomasBySearch<IDiplomaGetModel>(searchData)
      .subscribe((response) => {
        if (response) {
          let courses = response.Diploma;
          this.dataSource = new MatTableDataSource(courses);
          this.totalItems = response.countItems;
          this.dataSource.sort = this.sort;

          this.customFilter();
        }
        this.spinner.hide();
      });
  }

  onSubmit() {
    this.submitted = true;

    if (this.myForm.valid) {
      this.spinner.show();
      let Diploma: IDiplomaAddEditVM = {
        diploma_ar_name: this.FormCtrls.diploma_ar_name.value,
        diploma_en_name: this.FormCtrls.diploma_en_name.value,
        price: this.FormCtrls.price.value,
        from_date: this.FormCtrls.from_date.value,
        to_date: this.FormCtrls.to_date.value,
        diploma_description: this.FormCtrls.diploma_description.value,
        diploma_attach: this.FormCtrls.diploma_attach.value,
        categoryIds: this.FormCtrls.categoryIds.value,

        eduCompId: this.EduCompId,
      };
      // add or edit
      if (this.DiplomaId == 0) this.addDiploma(Diploma);
      else this.updateDiploma(Diploma);
    } else this.msg.error("General.FormNotValid");
  }

  addDiploma(Diploma: IDiplomaAddEditVM) {
    this.courseServ
      .addDiploma<IDiplomaAddEditVM, any>(Diploma)
      .subscribe((res) => {
        if (res?.returnValue == 1) {
          this.getAllDiplomas(1, false);
          this.dataSource.paginator = this.paginator;
          this.msg.success("General.AddSuccessfully");
          this.clearForm();
        } else {
          this.msg.error(res?.returnString);
        }
        this.spinner.hide();
      });
  }

  updateDiploma(Diploma: IDiplomaAddEditVM) {
    console.log(Diploma);

    this.courseServ
      .updateDiploma<IDiplomaAddEditVM, any>(this.DiplomaId, Diploma)
      .subscribe((res) => {
        if (res?.returnValue == 1) {
          this.getAllDiplomas(this.curPage, false);
          this.msg.success("General.UpdateSuccessfully");
          this.clearForm();
        } else {
          this.msg.error(res?.returnString);
        }
        this.spinner.hide();
      });
  }

  toEditDiploma(Diploma: IDiplomaGetVM) {
    this.DiplomaId = Diploma.id;
    this.myForm.patchValue(Diploma);

    this.FormCtrls.categoryIds.setValue(
      Diploma.category.map((category) => category.courseCategory_id)
    );
  }

  openDeleteDialog(Diploma: IDiplomaGetVM): void {
    let courseName = this.languageService.isArabic
      ? Diploma.diploma_ar_name
      : Diploma.diploma_en_name;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: courseName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeDiploma(Diploma.id);
      });
  }

  removeDiploma(id: number) {
    this.spinner.show();
    this.courseServ.removeDiploma(id).subscribe((response) => {
      if (response) {
        this.msg.success("General.DeleteSuccessfully");
        if (this.dataSource.data.length == 1 && this.curPage > 1) {
          // if last item in list: get previous page
          this.getAllDiplomas(this.curPage - 1, false);
          this.paginator.pageIndex = this.curPage - 1;
        } else {
          // if not last item in list: get current page
          this.getAllDiplomas(this.curPage, false);
        }
        if (this.DiplomaId == id) this.clearForm();
      }
      this.spinner.hide();
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  resetSearch() {
    this.myForm.reset();
    this.DiplomaId = 0;
    this.getAllDiplomas(1, false);
  }
  clearForm() {
    this.myForm.reset();
    this.submitted = false;
    this.DiplomaId = 0;
  }
}
