import { DatePipe } from "@angular/common";
import { Component, Inject, OnInit } from "@angular/core";
import {
  AbstractControlOptions,
  FormBuilder,
  FormGroup,
  Validators,
} from "@angular/forms";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { TranslateService } from "@ngx-translate/core";
import {
  IJobTitleDepartmentVM,
  IUpdateJobTitleDepartmentVM,
} from "app/admin/models/setup/job-title";
import { JobTitleService } from "app/admin/services/setup/job-title.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-update-job-department",
  templateUrl: "./update-job-department.component.html",
  styleUrls: ["./update-job-department.component.scss"],
})
export class UpdateJobDepartmentComponent implements OnInit {
  myForm: FormGroup;
  department: IJobTitleDepartmentVM;
  submitted: boolean = false;

  constructor(
    private translate: TranslateService,
    private fb: FormBuilder,
    private jobTitleService: JobTitleService,
    private toastr: MessagesService,
    private spinner: NgxSpinnerService,
    private datePipe: DatePipe,
    public languageService: LanguageService,
    public dialogRef: MatDialogRef<UpdateJobDepartmentComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    this.department = data.department;
  }

  ngOnInit(): void {
    this.myForm = this.fb.group(
      {
        from_date: [null, [Validators.required]],
        to_date: [null],
      },
      {
        validator: CustomeValidator.dateBefore("from_date", "to_date"),
      } as AbstractControlOptions
    );
    this.myForm.patchValue(this.department);
  }

  get formCtrl() {
    return this.myForm.controls;
  }

  update_JobTitle_Department() {
    this.submitted = true;
    // if (this.formCtrl.from_date.value == null) {
    //   this.toastr.warning("Must Select From Date");
    //   return;
    // }

    if (this.myForm.valid) {
      let department: IUpdateJobTitleDepartmentVM = {
        from_date: this.datePipe.transform(this.formCtrl.from_date.value),
        to_date: this.datePipe.transform(this.formCtrl.to_date.value),
      };

      this.spinner.show();
      this.jobTitleService
        .update_JobTitle_Department(
          department,
          this.department.job_title_dep_id
        )
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            this.toastr.success("General.UpdateSuccessfully");
            this.dialogRef.close(true);
          } else this.toastr.error(res.returnString);
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }
}
