import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import {
  IAddEditUserVM,
  ICourseGetVM,
  IGetUserCheckVM,
  IGetUsersVM,
  IuserAddFile,
  IUserListGetVM,
  searchUserVM,
} from "app/admin/models/admin/setup/course";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";

@Component({
  selector: "app-add-users-to-course",
  templateUrl: "./add-users-to-course.component.html",
  styleUrls: ["./add-users-to-course.component.scss"],
})
export class AddUsersToCourseComponent implements OnInit {
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  curPage: number = 1;
  EduCompId: number;
  itemCount: number = 0;
  courseId: number = 0;
  courseArName: string;
  courseEnName: string;

  dataSource: MatTableDataSource<IUserListGetVM>;
  displayedColumns: string[] = [
    "#",
    "name",
    "account_email",
    "mobile",
    "actions",
  ];
  myForm!: FormGroup;
  // lastUserIdSearch: number;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private authserv: AuthService,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private courseService: CourseService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private spinner: NgxSpinnerService,
    public languageService: LanguageService,
    public fileService: UploadFileService
  ) {
    route.paramMap.subscribe((params) => {
      this.courseId = Number(params.get("courseId"));

      this.courseService
        .getCourseById<ICourseGetVM>(this.courseId)
        .subscribe((res) => {
          if (res) {
            this.courseArName = res.sub_course.subCourse_ar_name;
            this.courseEnName = res.sub_course.subCourse_en_name;
          }
        });
    });

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              // console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });

    this.EduCompId = Number(this.authserv.getEduCompId());
  }

  ngOnInit() {
    this.myForm = this.fb.group({
      // page: [1],
      // subCourseId: [this.courseId],
      // EduCompId: [this.EduCompId],
      account_email: [null],
      mobile: [null],
      code: [null],
      excelFile: [null],
    });

    this.getAllUsers(this.curPage);
  }

  // get EduCompIdCtrl() {
  //   return this.myForm.get("EduCompId");
  // }
  // get courseIdCtrl() {
  //   return this.myForm.get("subCourseId");
  // }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      // this.EduCompIdCtrl.setValue(e.EduCompId);
      this.getAllUsers(1);
      this.dataSource.paginator = this.paginator;
    });

    // this.authserv.branchId.subscribe((e) => {
    //   this.getAllUsers(1);
    // });
  }

  startSortingTable() {
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "name":
          return this.languageService.isArabic
            ? item.ar_name ?? item.en_name
            : item.en_name ?? item.ar_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getAllUsers(page: number) {
    this.curPage = page;
    this.spinner.show();
    this.courseService
      .getAllUsers<IGetUsersVM>(page, this.courseId)
      .subscribe((res) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.userList);
          this.itemCount = res.countItems;
          this.startSortingTable();
          // console.log("Users", res.subUserList);
        }
        this.spinner.hide();
      });
  }

  checkIfUserExist() {
    if (
      !this.formCtls.account_email.value &&
      !this.formCtls.mobile.value &&
      !this.formCtls.code.value
    ) {
      this.toastr.warning("course.enterOneField");
      return;
    }

    let obj: searchUserVM = {
      account_email: this.formCtls.account_email.value?.trim() || null,
      mobile: this.formCtls.mobile.value?.trim() || null,
      code: this.formCtls.code.value || null,
    };

    this.spinner.show();
    this.courseService
      .checkUser<searchUserVM, IGetUserCheckVM>(obj, this.EduCompId)
      .subscribe((user: IGetUserCheckVM) => {
        // console.log("check user", user);
        if (user) {
          // this.lastUserIdSearch = user.userId;
          this.toastr.info("course.existUser");
        } else {
          this.toastr.info("course.notExistUser");
        }
        this.spinner.hide();
      });
  }

  addUser() {
    if (
      !this.formCtls.account_email.value &&
      !this.formCtls.mobile.value &&
      !this.formCtls.code.value
    ) {
      this.toastr.warning("course.enterOneField");
      return;
    }

    let obj: searchUserVM = {
      account_email: this.formCtls.account_email.value?.trim() || null,
      mobile: this.formCtls.mobile.value?.trim() || null,
      code: this.formCtls.code.value || null,
    };
    // console.log("form values add", this.myForm.value);

    this.spinner.show();
    this.courseService
      .checkUser<searchUserVM, IGetUserCheckVM>(obj, this.EduCompId)
      .subscribe((user: IGetUserCheckVM) => {
        // console.log("check user", user);
        if (!user) {
          this.toastr.warning("course.notExistUser");
          this.spinner.hide();
          return;
        } else {
          let obj: IAddEditUserVM = {
            userId: user.userId,
            subject_id: this.courseId,
          };
          // this.spinner.show();
          this.courseService
            .addUser<IAddEditUserVM, any>(obj)
            .subscribe((res: any) => {
              // console.log('adduser', res);
              if (res.returnValue == 0) this.toastr.error(res.returnString);
              else {
                this.myForm.reset();
                this.getAllUsers(1); // first page after add
                this.dataSource.paginator = this.paginator;
                this.toastr.success("General.AddSuccessfully");
              }
              this.spinner.hide();
            });
        }
      });
  }

  openDeleateDialog(user: IUserListGetVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.languageService.isArabic
            ? user.ar_name ?? user.en_name
            : user.en_name ?? user.ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteUser(user);
      });
  }

  deleteUser(user: IUserListGetVM) {
    this.spinner.show();
    this.courseService.deleteUser(user.id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.toastr.success("General.DeleteSuccessfully");
        if (this.dataSource.data.length == 1 && this.curPage > 1) {
          // if last item in list: get previous page
          this.getAllUsers(this.curPage - 1);
          this.paginator.pageIndex = this.curPage - 1;
        } else {
          // if not last item in list: get current page
          this.getAllUsers(this.curPage);
        }
      } else this.toastr.error(res.returnString);
      this.spinner.hide();
    });
  }

  uploadUsersByExcel() {
    if (this.formCtls.excelFile.value == null) {
      this.toastr.warning("course.excelSheet");
      return;
    } else {
      this.spinner.show();
      this.courseService
        .uploadUsersByExcel<IuserAddFile>({
          uploadedFile: this.formCtls.excelFile.value,
          subject_id: this.courseId,
        })
        .subscribe((res: any) => {
          // console.log(res);
          if (
            res?.returnValue == 1 ||
            (res?.isSendAllSuccess == false && res?.errors.length == 0)
          ) {
            this.toastr.success("General.UploadSuccessfully");
            this.getAllUsers(1); // first page after add
            this.dataSource.paginator = this.paginator;
          } else if (res?.returnValue == 0)
            this.toastr.error(res?.returnString);
          else if (res?.isSendAllSuccess == false) {
            this.toastr.error(res?.errors[0]);
            this.getAllUsers(1); // first page after add
            this.dataSource.paginator = this.paginator;
          } else if (res?.isUploaded == false)
            this.toastr.error(res?.errors[0]);
          this.spinner.hide();

          this.formCtls.excelFile.setValue(null);
          this.fileService.resetFileById("excel-file");
        });
    }
  }

  get formCtls() {
    return this.myForm.controls;
  }
}
