import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { giftSearchModel, giftViewModel } from "app/admin/models/admin/gifts";
import { GiftsService } from "app/admin/services/Admin/gifts.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";

//////ya rab nefla7
@Component({
  selector: "app-gifts-index",
  templateUrl: "./gifts-index.component.html",
  styleUrls: ["./gifts-index.component.scss"],
})
export class GiftsIndexComponent implements OnInit {
  functionId: number;
  myForm!: FormGroup;
  itemsCount: any;
  rowFunctions: IRowFunctionVM[];
  curPage: number = 1;
  EduCompId: any;
  // gifts: giftViewModel[] = [];
  dataSource: MatTableDataSource<giftViewModel>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  giftCategory: any[];

  displayedColumns: string[] = [
    "#",
    "englishName",
    "arabicName",
    "qty",
    "giftCategory",
    "points",
    "description",
    "actions",
  ];

  constructor(
    private fb: FormBuilder,
    private giftsService: GiftsService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private toastr: MessagesService,
    private authserv: AuthService,
    public languageService: LanguageService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      //    console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //  console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });

    this.EduCompId = this.authserv.getEduCompId();
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  ngOnInit() {
    this.myForm = this.fb.group({
      // page: [1],
      englishName: [null],
      arabicName: [null],
      giftsCategoryId: [null],
      points: [null],
      // EduCompId: [this.EduCompId],
    });

    this.getAllGiftCategory();
    this.getGiftsAll(this.curPage);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllGiftCategory();
      this.getGiftsAll(1);
      this.dataSource.paginator = this.paginator;
    });
  }

  startSortingTable() {
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "giftCategory":
          return this.languageService.isArabic
            ? item.arbGiftCategory ?? item.engGiftCategory
            : item.engGiftCategory ?? item.arbGiftCategory;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    /* if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    } */
  }

  getAllGiftCategory() {
    this.giftsService
      .getGiftCategoryDropDown(this.EduCompId)
      .subscribe((response: any) => {
        if (response) {
          this.giftCategory = response;
          // console.log(this.giftCategory);
        }
      });
  }

  getGiftsButton(page: number) {
    if (
      !this.FormCtrls.arabicName.value &&
      !this.FormCtrls.englishName.value &&
      !this.FormCtrls.giftsCategoryId.value &&
      !this.FormCtrls.points.value
    ) {
      this.toastr.warning("Gifts.enterOneField");
      return;
    } else {
      this.getGiftsSearch(page);
      this.dataSource.paginator = this.paginator;
    }
  }

  getGiftsSearch(page: number) {
    let searchData: giftSearchModel = {
      englishName: this.FormCtrls?.englishName.value?.trim().toLowerCase(),
      arabicName: this.FormCtrls?.arabicName.value?.trim().toLowerCase(),
      giftsCategoryId: this.FormCtrls?.giftsCategoryId.value,
      points: this.FormCtrls?.points.value,
    };
    // "" convert to null
    Object.keys(searchData).forEach((key, index) => {
      if (searchData[key] == "") searchData[key] = null;
    });

    this.curPage = page;
    this.spinner.show();
    this.giftsService
      .getGiftsSearch(this.EduCompId, page, searchData)
      .subscribe((res: any) => {
        if (res) {
          // this.gifts = res.giftsList;
          // console.log("categories", this.gifts);
          this.dataSource = new MatTableDataSource(res.giftsList);
          this.itemsCount = res.countItems;
          this.startSortingTable();
        }
        this.spinner.hide();
      });
  }

  getGiftsAll(page: number) {
    this.curPage = page;
    this.spinner.show();
    this.giftsService
      .getGiftsAll(this.EduCompId, page)
      .subscribe((res: any) => {
        if (res) {
          // this.gifts = res.giftList;
          // console.log("gifts", this.gifts);
          this.dataSource = new MatTableDataSource(res.giftList);
          this.itemsCount = res.countItems;
          this.startSortingTable();
        }
        this.spinner.hide();
      });
  }

  openDeleteDialog(gift: giftViewModel): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.languageService.isArabic
            ? gift.arabicName ?? gift.englishName
            : gift.englishName ?? gift.arabicName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeGiftCategory(gift);
      });
  }

  removeGiftCategory(gift: giftViewModel) {
    this.spinner.show();
    this.giftsService.RemoveGift(gift.id).subscribe((response: any) => {
      if (response.returnValue == 2) {
        this.toastr.error("Gifts.giftDeleteError");
      } else {
        this.toastr.success("General.DeleteSuccessfully");
        if (this.dataSource.data.length == 1 && this.curPage > 1) {
          // if last item in list: get previous page
          this.getGiftsAll(this.curPage - 1);
          this.paginator.pageIndex = this.curPage - 1;
        } else {
          // if not last item in list: get current page
          this.getGiftsAll(this.curPage);
        }
      }
      this.spinner.hide();
    });
  }

  resetSearch() {
    this.myForm.reset();
    this.getGiftsAll(1);
    this.dataSource.paginator = this.paginator;
  }
}
