import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class ManageViewsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  searchStudent(EduCompId: any, body: any) {
    return this.post(`Admin/searchUSer/${EduCompId}`, body);
  }

  resetViews(EduCompId: any, body: any) {
    return this.post(`Admin/resetViewsCount/${EduCompId}`, body);
  }

  addExceptionalViews(EduCompId: any, body: any) {
    return this.post(`Admin/ExceptionalViewsResetCount/${EduCompId}`, body);
  }
}
