import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";

import { IArticleViewModel } from "app/admin/models/admin/setup/articles";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class ArticlesService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getAllArticles<T>(page: number, EduCompId: number) {
    return this.get<IArticleViewModel>(
      `Article/geAllArticles/${EduCompId}/${page}`
    );
  }

  addArticle<T, TV>(obj: T) {
    return this.post<TV>(`Article/PostArticles`, obj);
  }

  editArticle<T, TV>(obj: T, id: number) {
    return this.put<TV>(`Article/PutArticles/${id}`, obj);
  }

  editJobTitleArticle<T, TV>(id: number, obj: T) {
    return this.put<TV>(`Article/EditArticleDepJob/${id}`, obj);
  }

  deleteArticle(id: any) {
    return this.delete(`Article/DeleteArticles/${id}`);
  }

  deleteJobTitleArticle(id: any) {
    return this.delete(`Article/DeleteJobTitleOfArticles/${id}`);
  }
}
